const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			formNode: {
				title: '',
				content: '',
				board_id: '',
				topic_id: ''
			}
		}
	},
	onLoad(e) {
		console.log(e)
		this.formNode.board_id = e.id
	},
	methods: {
		inputValue(e){
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
		},
		// 发帖
		post(){
			uni.request({
				url: `${helper.requestUrl}/posts/send`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.formNode,
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res);
					if (res.data.status_code == '200') {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						});
						setTimeout(() => {
							uni.reLaunch({
								url: `/pages/index`
							})
						}, 2000)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						});
					}
				}
			});
		}
	}
}
